
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'Setting',
components: {
XSwitch,
Group,
XButton
},
data () {
return {
newMessage: true
}
},
methods: {
backTo () {
window.AndroidListener.onBackListener()
},
getBack () {
this.$router.go(-1)
},
accountBind () {
this.$router.push({
name: 'accountBind'
})
},
setPayPassword () {
this.$router.push({
name: 'setPayPassword'
})
},
setPassword () {
this.$router.push({
name: 'setPassword'
})
}
}
}



